<template>
    <div class="content">
      <div v-if="isShowAlert" class="box-alert" :class="{
        'success': isTransactionSuccess,
        'error': !isTransactionSuccess
      }">
        <i class="el-icon-close close-icon" @click="isShowAlert = false"></i>
        <div style="display: flex; flex-direction: row">
          <div style="width: 160px">
            <span v-if="isTransactionSuccess">
              <i class="el-icon-success"></i> SUCCESS
            </span>
            <span v-else><i class="el-icon-error"></i> FAILED</span>
          </div>
          <div style="padding-top: 5px">
            <span style="color: #dddddd; font-size: 15px;">
              View detail on
              <a :href="`${BscScanUrl}/tx/${txId}`" target="_blank" style="color: #dddddd">BscScan
                <i class="el-icon-link font-bold" style="font-size: 18px"></i>
              </a>
            </span>
          </div>
        </div>
      </div>
      <div>
        <h1 class="h1-label text-color-gredient font-normal" style="text-align: center; width: 100%">SUBSCRIBE ORACLE</h1>
      </div>
      <div class="subscribe-content">
        <h3 class="h1-label text-color-gredient font-bold" style="width: 100%">THAI GOVERNMENT LOTTERY</h3>
        <div class="input-subscribe">
          <div class="text-color-gredient font-bold input-subscribe-label">D-App Address</div>
          <el-input v-model="dAppAddress" @input="onInputDAppAddress"></el-input>
        </div>
        <div class="input-subscribe">
          <div class="text-color-gredient font-bold input-subscribe-label">Purchase Day</div>
          <el-input v-model="purchaseDay" @input="filterNumberOnly"></el-input>
          <div v-if="parseInt(purchaseDay) > 0" class="purchase-price-detail">
            {{purchaseDay}} Day = {{calculateKookleByPurchaseDay()}} KOOKL
          </div>
        </div>
        <div>
          <div v-if="parseInt(allowance) === 0">
            <el-button @click="onApproveKookleTokenButtonClick"
                       type="primary" style="font-size: 20px; width: 100%; border-radius: 10px;">Approve</el-button>
          </div>
          <div v-else>
            <el-button @click="onSubscribeOracleButtonClick"
                       :class="{'btn-disable': isDisableSubscribeButton}"
                       :disabled="isDisableSubscribeButton"
                       type="primary" style="font-size: 20px; width: 100%; border-radius: 10px;">Subscribe</el-button>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
import { get } from 'lodash'
import Web3 from 'web3'
import kookleTokenAbi from '@/abi/KookleToken.json'
import subscribeOracleAbi from '@/abi/SubscribeOracle.json'

const kookleTokenAddress = process.env.VUE_APP_CONTRACT_KOOKLE_TOKEN
const subscribeOracleAddress = process.env.VUE_APP_CONTRACT_SUBSCRIBE
const oracleNameLotteryThai = process.env.VUE_APP_ORACLE_NAME_LOTTERY_THAI
const BscScanUrl = process.env.VUE_APP_BSC_SCAN_URL

export default {
  name: 'SubscribeLotteryThai',
  data () {
    return {
      BscScanUrl,
      web3: null,
      eth: null,
      contract: null,
      isWalletConnected: false,
      accountAddressList: [],
      currentAddress: '',
      allowance: false,
      kookleTokenInstance: null,
      subscribeOracleInstance: null,
      dAppAddress: '',
      purchaseDay: '',
      purchaseAmount: '',
      isDisableSubscribeButton: true,
      purchaseSetting: {
        ratePerDay: 0,
        minExtendTime: 1
      },
      payValue: 0,
      txId: '',
      isShowAlert: false,
      isTransactionSuccess: true
    }
  },
  async created () {
    this.eth = window.ethereum

    if (!this.isWalletConnected) {
      await this.connectWallet()
    }

    try {
      this.web3 = new Web3(this.eth)
      this.kookleTokenInstance = new this.web3.eth.Contract(kookleTokenAbi, kookleTokenAddress)
      this.subscribeOracleInstance = new this.web3.eth.Contract(subscribeOracleAbi, subscribeOracleAddress)

      this.allowance = await this.kookleTokenInstance.methods.allowance(this.currentAddress, subscribeOracleAddress).call()
      const purchaseSetting = await this.subscribeOracleInstance.methods.getPurchaseSetting(oracleNameLotteryThai).call()
      const ratePerDay = get(purchaseSetting, '[0]', '1000000000000000000')

      this.purchaseSetting.ratePerDay = this.web3.utils.fromWei(ratePerDay)
      this.purchaseSetting.minExtendTime = get(purchaseSetting, '[1]', 1)

      // console.log('purchaseSetting', this.purchaseSetting)
    } catch (err) {
      console.log(err.message)
    }
  },
  methods: {
    connectWallet: async function () {
      this.isWalletConnected = await this.ethEnabled()
      this.accountAddressList = await window.web3.eth.getAccounts()
      this.currentAddress = this.accountAddressList[0]
    },
    ethEnabled: async function () {
      if (window.ethereum) {
        window.web3 = new Web3(window.ethereum)
        await window.ethereum.enable()

        return true
      }

      return false
    },
    onApproveKookleTokenButtonClick: async function () {
      try {
        const approveAmount = this.web3.utils.toWei('1000000000')
        await this.kookleTokenInstance.methods.approve(subscribeOracleAddress, approveAmount).send({
          from: this.currentAddress
        })

        this.allowance = await this.kookleTokenInstance.methods.allowance(this.currentAddress, subscribeOracleAddress).call()
      } catch (err) {
        this.isTransactionSuccess = false
        this.isShowAlert = true
      }
    },
    onSubscribeOracleButtonClick: async function () {
      if (this.purchaseDay === '') {
        alert('Please enter purchase day')
        return false
      }

      try {
        const payValue = this.web3.utils.toWei(this.payValue.toString())
        const result = await this.subscribeOracleInstance.methods.purchaseByKookle(oracleNameLotteryThai, this.dAppAddress, payValue).send({
          from: this.currentAddress
        })

        this.txId = get(result, 'transactionHash', '')
        this.isShowAlert = true
        this.isTransactionSuccess = true
      } catch (err) {
        this.isTransactionSuccess = false
        this.isShowAlert = true
      }
    },
    onInputDAppAddress: function () {
      this.isDisableSubscribeButton = this.validateIsSubscribeButtonDisable()
    },
    filterNumberOnly: function () {
      this.purchaseDay = this.purchaseDay.replace(/[^0-9]+/g, '')
      this.isDisableSubscribeButton = this.validateIsSubscribeButtonDisable()
    },
    validateIsSubscribeButtonDisable: function () {
      return !(this.dAppAddress !== '' && this.purchaseDay !== '' && parseInt(this.purchaseDay) >= parseInt(this.purchaseSetting.minExtendTime))
    },
    calculateKookleByPurchaseDay: function () {
      const purchaseDay = parseInt(this.purchaseDay)
      const ratePerDay = parseInt(this.purchaseSetting.ratePerDay)

      this.payValue = purchaseDay * ratePerDay
      return this.payValue
    }
  }
}
</script>

